html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

.maintenance-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #f8f9fa 60%, #e0e7ef 100%);
  color: #333;
}

.maintenance-container h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
  text-shadow: 0 2px 8px rgba(211,47,47,0.08);
}

.maintenance-container p {
  font-size: 1.25rem;
  line-height: 2.2;
  margin: 0;
  text-align: center;
  background: rgba(255,255,255,0.85);
  border-radius: 16px;
  padding: 2rem 2.5rem;
  box-shadow: 0 4px 24px rgba(0,0,0,0.07);
  font-weight: 500;
  letter-spacing: 0.02em;
}

.maintenance-container strong {
  color: #1976d2;
  font-weight: bold;
} 